
body {
  background: #DFCEBA;
  color: #11100E;

  Font-family: system-ui, Helvetica, "Helvetica Neue", "Franklin Gothic", "ITC Franklin Gothic",Arial ,sans-serif;
  font-size: 25px;
  line-height: 40px;
  transition: font-size 0.8s;
}

::selection {
  background-color: red;
}

/* for pages */
.page-switcher {
  text-decoration: none;
  display: block;
}

.outro {
  padding:  100px 50px;
  text-align: center;
  font-size: 1em;
}

.outro a {
  color: #b6a590;
}

.outro a:hover {
  color: red;
}

@media only screen and (max-width: 500px) {
  body {
    font-size: 20px;
  }
}

@media only screen and (max-width: 450px) {
  body {
    font-size: 18px;
  }
}

@media only screen and (max-width: 450px) {
  body {
    font-size: 16px;
    line-height: 20px;
  }
}

@media only screen and (max-width: 350px) {
  .outro {
    font-size: 0.9em;
  }
}